import React, { FunctionComponent } from "react";
import { Button, Input } from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Redirect } from "react-router-dom";
import styles from './reset.module.css';
import { useDispatch, useSelector } from "../services/types/hooks";
import { setNewPassword } from "../services/actions/user-data";
import { getCookie } from "../utils/utils";

export const ResetPage: FunctionComponent = () => {
  const dispatch = useDispatch();
  const { isPasswordChanged, resetAnswer, userName } = useSelector(store => store.userState)
  const [passwordInputValue, setPasswordInputValue] = React.useState('');
  const [tokenInputValue, setTokenInputValue] = React.useState('');
  const [error, setError] = React.useState(false);
  const onPasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPasswordInputValue(e.target.value)
  }

  const onTokenChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setTokenInputValue(e.target.value)
  }
  const [visible, setVisible] = React.useState(false);
  const inputRef = React.useRef<HTMLInputElement>(null);

  const onIconClick = () => {
    setVisible(true);
    setTimeout(() => inputRef.current?.focus(), 0);
  };
  const validateField = (value: string) => {
    setError(value.length < 6);
  };

  const onBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    if (e.target.value) {
      validateField(e.target.value);
    } else {
      setError(false);
    }
    setVisible(false);
  };

  const resetUserPassword = (evt: React.FormEvent<HTMLFormElement>, password: string, token: string) => {
    evt.preventDefault();
    dispatch(setNewPassword(password, token))
  }

  if (!resetAnswer) {
    return (
      <Redirect to='/login' />
    )
  }

  if (userName) {
    return (
      <Redirect to='/' />
    )
  }

  return (
    <div className={styles.pagestyle}>
      <form className={styles.form} onSubmit={evt => resetUserPassword(evt, passwordInputValue, tokenInputValue)}>
        <fieldset className={styles.inputsfield}>
          <h2 className={`text text_type_main-medium ${styles.header}`}>–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è</h2>
          <Input errorText={'–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –ø–∞—Ä–æ–ª—å'} error={error} ref={inputRef} icon={visible ? 'HideIcon' : 'ShowIcon'} type={visible ? 'text' : 'password'} onChange={onPasswordChange} onBlur={onBlur} onIconClick={onIconClick} value={passwordInputValue} name={'password'} placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤—ã–π –ø–∞—Ä–æ–ª—å' />
          <Input onChange={onTokenChange} value={tokenInputValue} name={'password'} placeholder='–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥ –∏–∑ –ø–∏—Å—å–º–∞' />
        </fieldset>
        <Button type="primary" size="medium" >{isPasswordChanged ? '–ü–∞—Ä–æ–ª—å –∏–∑–º–µ–Ω–µ–Ω' : '–°–æ—Ö—Ä–∞–Ω–∏—Ç—å'}</Button>
        <div className={`${styles.redirectmenu}  mt-20`}>
          <p className={`${styles.password} text text_type_main-default text_color_inactive `}>–í—Å–ø–æ–º–Ω–∏–ª–∏ –ø–∞—Ä–æ–ª—å?</p>
          <Link className={`${styles.link} text text_type_main-default`} to='/login'>–í–æ–π—Ç–∏</Link>
        </div>
      </form>

    </div>
  )
}
