import React, { FunctionComponent } from "react";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components";
import ingredientsStyles from './burger-ingredients.module.css';
import { IngredientType } from '../ingredient-type/ingredient-type';
import { useSelector, useDispatch } from '../../services/types/hooks';
import { useHistory, Route } from "react-router-dom";




export const BurgerIngredients: FunctionComponent = () => {

  const { ingredients } = useSelector(state => state.burgerData);


  const [current, setCurrent] = React.useState('–ë—É–ª–∫–∏')
  const bunsRef = React.useRef<HTMLDivElement>(null);
  const soucesRef = React.useRef<HTMLDivElement>(null);
  const fillingsRef = React.useRef<HTMLDivElement>(null);

  ///–≤—ã—á–∏—Å–ª—è–µ–º –≥–¥–µ —Å–µ–π—á–∞—Å –Ω–∞—Ö–æ–¥–∏—Ç—Å—è —Å–∫—Ä–æ–ª–ª –∏ –ø–æ–¥—Å–≤–µ—á–∏–≤–∞–µ–º –Ω—É–∂–Ω—ã–π Tab
  function determineElementPosition(e: React.ChangeEvent<HTMLDivElement>) {
    const scrollPosition = e.target.scrollTop;
    const positionOfSouceContainer = soucesRef.current !== null && soucesRef.current.offsetTop;
    const positionofFillingsContainer = fillingsRef.current !== null && fillingsRef.current.offsetTop;
    if (scrollPosition + 303 <= positionOfSouceContainer) {
      setCurrent('–ë—É–ª–∫–∏');
    }
    else if (scrollPosition + 303 <= positionofFillingsContainer) {
      setCurrent('–°–æ—É—Å—ã');
    } else {
      setCurrent('–ù–∞—á–∏–Ω–∫–∏');
    }
  }

  return (

    <div className={ingredientsStyles.burger__tabs}>
      <h1 className='text text_type_main-large mt-10 mb-5'>–°–æ–±–µ—Ä–∏—Ç–µ –±—É—Ä–≥–µ—Ä</h1>
      <ul className={`mb-10 ${ingredientsStyles.burger__nav}`}>
        <li>
          <Tab value="–ë—É–ª–∫–∏" active={current === '–ë—É–ª–∫–∏'} onClick={(value) => { setCurrent(value); bunsRef.current !== null && bunsRef.current.scrollIntoView({ behavior: "smooth", }) }} >
            –ë—É–ª–∫–∏
          </Tab>
        </li>
        <li>
          <Tab value="–°–æ—É—Å—ã" active={current === '–°–æ—É—Å—ã'} onClick={(value) => { setCurrent(value); soucesRef.current !== null && soucesRef.current.scrollIntoView({ behavior: "smooth", }) }}>
            –°–æ—É—Å—ã
          </Tab>
        </li>
        <li>
          <Tab value="–ù–∞—á–∏–Ω–∫–∏" active={current === '–ù–∞—á–∏–Ω–∫–∏'} onClick={(value) => { setCurrent(value); fillingsRef.current !== null && fillingsRef.current.scrollIntoView({ behavior: "smooth", }) }}>
            –ù–∞—á–∏–Ω–∫–∏
          </Tab>
        </li>
      </ul>
      <div className={ingredientsStyles.burger__ingredients} onScroll={(e: any) => { determineElementPosition(e) }}>
        <IngredientType ingredientName='–ë—É–ª–∫–∏' data={ingredients.filter(item => item.type === 'bun')} ref={bunsRef} />
        <IngredientType ingredientName='–°–æ—É—Å—ã' data={ingredients.filter(item => item.type === 'sauce')} ref={soucesRef} />
        <IngredientType ingredientName='–ù–∞—á–∏–Ω–∫–∏' data={ingredients.filter(item => item.type === 'main')} ref={fillingsRef} />
      </div>

    </div>
  )
}



